  flex: 1;
  align-items: stretch;
  margin-bottom: 5px;
  padding: 16px 0 3px;
  font-size: 14px;
  display: flex;
  position: relative;
}

.UIField-module__label_lh-KDa__0197 {
  cursor: text;
  color: #ffffff80;
  pointer-events: none;
